/**
 * Border
 * ------
 */

@import "../abstracts/variables";

$__border-color-map: (
        "blue": $_DEFAULT_BLUE,
        "green": $_DEFAULT_GREEN,
        "indigo": $_DEFAULT_INDIGO,
        "orange": $_DEFAULT_ORANGE,
        "pink": $_DEFAULT_PINK,
        "purple": $_DEFAULT_PURPLE,
        "red": $_DEFAULT_RED,
        "teal": $_DEFAULT_TEAL,
        "yellow": $_DEFAULT_YELLOW,
        "gray": $_DEFAULT_GRAY-1,
        "gray-1": $_DEFAULT_GRAY-6,
        "gray-2": $_DEFAULT_GRAY-5,
        "gray-3": $_DEFAULT_GRAY-4,
        "gray-4": $_DEFAULT_GRAY-3,
        "gray-5": $_DEFAULT_GRAY-2,
        "gray-6": $_DARK_GRAY-2,
        "gray-7": $_DARK_GRAY-3,
        "gray-8": $_DARK_GRAY-4,
        "gray-9": $_DARK_GRAY-5,
        "gray-10": $_DARK_GRAY-6
);

$__border-style-map: (
        "none",
        "hidden",
        "dotted",
        "dashed",
        "solid",
        "double",
        "groove",
        "ridge",
        "inset",
        "outset"
);

$__border-width-map: (
        "1": $_DEFAULT_BORDER-WIDTH-1,
        "2": $_DEFAULT_BORDER-WIDTH-2,
        "3": $_DEFAULT_BORDER-WIDTH-3,
        "4": $_DEFAULT_BORDER-WIDTH-4,
        "5": $_DEFAULT_BORDER-WIDTH-5
);

$__border-radius-map: (
        "sm": $_DEFAULT_EDGE-2,
        "md": $_DEFAULT_EDGE-3,
        "lg": $_DEFAULT_EDGE-4,
        "xl": $_DEFAULT_EDGE-5,
        "full": $_DEFAULT_EDGE-6,
        "none": $_DEFAULT_EDGE-1
);

$__border-position-map: (
        "top",
        "right",
        "bottom",
        "left"
);

/**
 * Border
 * The border class `.b` will set a basic border of 1px solid with a light gray background color.
 */
.b {
  border: 1px solid map_get($__border-color-map, "gray");

  /**
  * Border Color
  * Overrides the default gray color of the initialized border.
  */
  @each $__border-color, $__border-color-value in $__border-color-map {
    .bc-#{$__border-color} {
      border-color: unquote($__border-color-value) !important;
    }
  }

  /**
   * Border Style
   * Overrides the default border style.
   */
  @each $__border-style in $__border-style-map {
    .bs-#{$__border-style} {
      border-style: unquote($__border-style) !important;
    }
  }

  /**
   * Border Width (Weight)
   */
  @each $__border-width, $border-width-value in $__border-width-map {
    .bw-#{$__border-width} {
      border-width: unquote($border-width-value) !important;
    }
  }

  /**
   * Border Radius (Edge)
   */
  @each $__border-radius, $__border-radius-value in $__border-radius-map {
    .be-#{$__border-radius} {
      border-radius: unquote($__border-radius-value) !important;
    }
  }
}
